<template>
	<div v-bind="$attrs"></div>
</template>

<script>
import EasterEggTriggerCore from '../plugin/index';

export default {
	name: 'EasterEggComponent',
	props: {
		callback: {
			default: null,
			type: Function,
		},
		destroyBus: {
			default: false,
			type: Boolean,
		},
		pattern: {
			default: () => ['ArrowUp', 'ArrowUp', 'ArrowDown', 'ArrowDown', 'ArrowLeft', 'ArrowRight', 'ArrowLeft', 'ArrowRight', 'b', 'a'],
			type: Array,
		},
		target: {
			default: 'body',
			type: String,
		},
		triggered: {
			default: null,
			type: Function,
		},
		type: {
			default: 'keydown',
			type: String,
		},
	},
	data: () => ({
		defaultOptions: {
			delay: 500,
		},
		// callback: null,
		// destroyBus: false,
		// name: 'easter-egg',
		// pattern: ['ArrowUp', 'ArrowUp', 'ArrowDown', 'ArrowDown', 'ArrowLeft', 'ArrowRight', 'ArrowLeft', 'ArrowRight', 'b', 'a'],
		// target: 'div',
		// triggered: null,
		// type: 'keydown',
		// withBus: true,
	}),
	computed: {},
	watch: {},
	created() {
		console.log('EasterEggComponent created');
	},
	mounted() {
		this.buildOptions();
	},
	methods: {
		buildOptions() {
			const defaultOptions = EasterEggTriggerCore.defaultOptions;

			const options = {
				callback: this.callback,
				destroyBus: false,
				name: 'easter-egg',
				pattern: this.pattern,
				target: this.target,
				triggered: this.triggered,
				type: this.type,
				withBus: true,
			};

			this.init(options);
		},
		init(options) {
			EasterEggTriggerCore.init(this.defaultOptions, options);
		},
	},
};
</script>
